@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* * {
  border: 1px solid #f00 !important;
} */

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #303540;
}

/* Heading */
.header1, .header2 {
  position: relative;
  text-align: center;
  margin: 0;
}

.header1 {
  color: #ffffff;
  left: 0;
  right: 0;
  top: 0;
  margin: 40px auto;
  justify-content: center;
  position: absolute;
  letter-spacing: 4px;
  z-index: 1;
}

.header2 {
  color: #61DAFB;
  font-size: 70px;
  margin: 35px auto 0;
  background-color: #282C34;
}

.footer {
  color: #b3b5b9;
  text-align: center;
  font-size: 12px;
  margin: 15px auto 0;
  padding: 30px;
  background-color: #282C34;
  white-space: pre-wrap;
}

/* Main Projects */
.container {
  max-width: 600px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}

ul {
  margin: 20px;
  list-style: none;
}

.container > * {
  flex-basis: 100%;
}

.P3 {
  margin: 15px auto;
  padding: 10px;
  background-color: #282C34;
  height: auto;
  border-radius: 12px;
}

.P3 h1 {
  color: #ffffff;
  font-size: 30px;
  margin: 0 auto 13px;
}

.P3 .inputBox {
  display: flex;
}

.P3-Input {
  background-color: #FAFAFA;
  border: none;
  height: 50px;
  width: 95%;
  outline: none;
}

.P3-Input[type='text'] {
  font-size: 22px;
  padding: 7px 15px;
}

.P3 button {
  font-weight: 700;
  border: none;
  text-decoration: none;
  color: #282C34;
  background-color: #61DAFB;
}

.P3 h3 {
  font-size: 50px;
  color: #ffffff;
  margin: 10px auto;
  overflow-wrap: break-word;
}

.P3 p {
  line-height: 2px;
  color: #ffffff;
  
}
.P1 {
  margin: 15px auto;
  padding: 10px;
  background-color: #282C34;
  height: 255px;
  border-radius: 12px;
}

.P1 h1 {
  color: #ffffff;
  font-size: 30px;
  margin: 0 auto 13px;
}

.P1-Input {
  background-color: #FAFAFA;
  border: 1px solid;
  height: 50px;
  width: 90%;
  outline: none;
  margin: 3px;
  border-radius: 5px;
}

.P1-Input[type='text'] {
  font-size: 22px;
  padding: 7px;
}

.P1 .Set {
  display: flex;
}

.P1 .currency {
  font-family: 'Roboto Mono', monospace;
  color: #61DAFB;
  font-size: 21px;
  text-align: left;
  vertical-align: middle;
  margin: 2px 4px;
  padding: 10px 0px;
}
.P2 {
  margin: 15px auto;
  padding: 10px;
  background-color: #282C34;
  height: auto;
  border-radius: 12px;
}

.P2 h1 {
  color: #ffffff;
  font-size: 30px;
  margin: 0 auto 13px;
}

.P2 .inputBox {
  display: block;
}

.P2-Input {
  background-color: #FAFAFA;
  border: none;
  height: 50px;
  width: 100%;
  outline: none;
  border-radius: 5px;
}

.P2-Input[type='number'] {
  font-size: 22px;
  padding: 7px 15px;
}

.P2 button {
  display: block;
  margin: 20px auto 30px;
  height: 50px;
  width: 150px;
  justify-content: center;
  font-weight: 700;
  border: none;
  text-decoration: none;
  color: #282C34;
  background-color: #61DAFB;
  border-radius: 30px;
}

.P2 p {
  line-height: 2px;
  color: #ffffff;
  margin: 15px 2px;
}

.P2 .BMIResult {
  font-size: 30px;
}
